<template>
  <div class="container mx-auto p-4 md:p-6 h-screen flex flex-col md:flex-row">
    <Controls
      v-bind="{ exploded, animateColors }"
      @startOuter="rotateOuter"
      @pauseOuter="stopRotatingOuter"
      @resetOuter="resetOuter"
      @startInner="rotateInner"
      @pauseInner="stopRotatingInner"
      @resetInner="resetInner"
      @animateColors="animateColors = !animateColors"
      @explode="exploded = !exploded"
    />
    <ViewPort v-bind="{ perspective, exploded, outerTransform, innerTransform, animateColors }" />
  </div>
</template>

<script>
import Controls from './components/Controls'
import ViewPort from './components/ViewPort.vue'
import { setInterval, clearInterval } from 'timers'

const identityTransform = 'rotateX(0) rotateY(0) rotateZ(0)'

export default {
  name: 'App',
  components: {
    Controls,
    ViewPort
  },
  data() {
    return {
      perspective: 0,
      exploded: false,
      outerTransform: identityTransform,
      innerTransform: identityTransform,
      outerTransformInterval: undefined,
      innerTransformInterval: undefined,
      animateColors: false
    }
  },
  computed: {
    identityTransform() {
      return identityTransform
    }
  },
  methods: {
    randomRotation() {
      const x = Math.round(Math.random() * 360)
      const y = Math.round(Math.random() * 360)
      const z = Math.round(Math.random() * 360)

      return `rotateX(${x}deg) rotateY(${y}deg) rotateZ(${z}deg)`
    },
    rotateOuter() {
      this.outerTransform = this.randomRotation()

      this.outerTransformInterval = setInterval(() => {
        this.outerTransform = this.randomRotation()
      }, 2000)
    },
    stopRotatingOuter() {
      clearInterval(this.outerTransformInterval)
    },
    resetOuter() {
      this.stopRotatingOuter()
      this.outerTransform = this.identityTransform
    },
    rotateInner() {
      this.innerTransform = this.randomRotation()

      this.innerTransformInterval = setInterval(() => {
        this.innerTransform = this.randomRotation()
      }, 4000)
    },
    stopRotatingInner() {
      clearInterval(this.innerTransformInterval)
    },
    resetInner() {
      this.stopRotatingInner()
      this.innerTransform = this.identityTransform
    },
    setPerspective() {
      const w = window.innerWidth
      const h = window.innerHeight

      const perspective =
        Math.pow(((w / 2) * w) / 2 + ((h / 2) * h) / 2, 0.5) / Math.tan((30 * Math.PI) / 180)

      this.perspective = `${perspective}px`
    }
  },
  mounted() {
    this.setPerspective()
    window.addEventListener('resize', this.setPerspective)
  }
}
</script>
