<template>
  <div
    class="border w-full h-full bg-white rounded shadow overflow-hidden flex justify-center items-center"
    :style="{ perspective }"
  >
    <div class="cube" :style="{ transform: outerTransform }">
      <div
        v-for="(face, index) in faces"
        :key="index"
        class="face opacity-75 border border-black"
        :class="[{ exploded }, face, colorAt(index)]"
      ></div>

      <div class="inner-cube" :style="{ transform: innerTransform }">
        <div
          v-for="(face, index) in faces"
          :key="index"
          class="inner-face border border-black"
          :class="`inner-${face}`"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import { clearInterval, setInterval } from 'timers'
export default {
  name: 'ViewPort',
  props: {
    perspective: [Number, String],
    exploded: Boolean,
    outerTransform: String,
    innerTransform: String,
    animateColors: Boolean
  },
  data() {
    return {
      faces: ['top', 'bottom', 'left', 'right', 'front', 'back'],
      availableColors: [
        'gray',
        'red',
        'orange',
        'yellow',
        'green',
        'teal',
        'blue',
        'indigo',
        'purple',
        'pink'
      ],
      originalColors: [
        ['blue', '500'],
        ['red', '500'],
        ['green', '500'],
        ['purple', '500'],
        ['teal', '500'],
        ['orange', '500']
      ],
      colors: [],
      colorInterval: undefined
    }
  },
  methods: {
    colorAt(index) {
      return `bg-${this.colors[index][0]}-${this.colors[index][1]}`
    }
  },
  watch: {
    animateColors(animate) {
      if (animate) {
        this.colorInterval = setInterval(() => {
          this.colors = this.availableColors
            .map(c => [c, Math.ceil(Math.random() * 3) * 100 + 400])
            .sort(() => Math.random() - 0.5)
        }, 500)
      } else {
        clearInterval(this.colorInterval)
        this.colors = this.originalColors
      }
    }
  },
  created() {
    this.colors = this.originalColors
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.cube,
.inner-cube {
  position: relative;
  transform-style: preserve-3d;
}

.cube {
  transition: transform 2s linear;
}

.inner-cube {
  transition: transform 4s linear;
}

.face {
  width: 200px;
  height: 200px;
  position: absolute;
  top: calc(50% - 100px);
  left: calc(50% - 100px);
  transition: transform 0.5s ease-in, background-color 0.5s ease;
}

.inner-face {
  width: 100px;
  height: 100px;
  position: absolute;
  top: calc(50% - 50px);
  left: calc(50% - 50px);
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}

.top {
  transform: translateY(-100px) rotateX(90deg);

  &.exploded {
    transform: translateY(-200px) rotateX(90deg);
  }
}

.bottom {
  transform: translateY(100px) rotateX(-90deg);

  &.exploded {
    transform: translateY(200px) rotateX(-90deg);
  }
}

.left {
  transform: translateX(-100px) rotateY(-90deg);

  &.exploded {
    transform: translateX(-200px) rotateY(-90deg);
  }
}

.right {
  transform: translateX(100px) rotateY(90deg);

  &.exploded {
    transform: translateX(200px) rotateY(90deg);
  }
}

.front {
  transform: translateZ(100px) rotateY(0);

  &.exploded {
    transform: translateZ(200px) rotateY(0);
  }
}

.back {
  transform: translateZ(-100px) rotateY(180deg);

  &.exploded {
    transform: translateZ(-200px) rotateY(180deg);
  }
}

.inner-top,
.inner-bottom {
  background-image: url('../assets/tailwind.png');
}

.inner-top {
  transform: translateY(-50px) rotateX(90deg);
}

.inner-bottom {
  transform: translateY(50px) rotateX(-90deg);
}

.inner-left,
.inner-right {
  background-image: url('../assets/vue.png');
}

.inner-left {
  transform: translateX(-50px) rotateY(-90deg);
}

.inner-right {
  transform: translateX(50px) rotateY(90deg);
}

.inner-front,
.inner-back {
  background-image: url('../assets/laravel.png');
}

.inner-front {
  transform: translateZ(50px) rotateY(0);
}

.inner-back {
  transform: translateZ(-50px) rotateY(180deg);
}
</style>
